<template>
  <div class="dialog">
    <md-dialog :md-active.sync="showDialog">
      <md-dialog-title>
         
        <span @click="showDialog = false">
            <md-icon class="fa fa-times dialog__close"></md-icon>
        </span>
      </md-dialog-title>
      <div class="dialog__content">
        <md-autocomplete v-model="item.name" :md-options="items">
            <label></label>
        </md-autocomplete>
        <md-field>
            <label></label>
            <md-input v-model="item.count" type="number"></md-input>
        </md-field>
      </div>
      <md-dialog-actions>
        <md-button class="md-raised md-green" @click="showDialog=false"></md-button>
      </md-dialog-actions>
    </md-dialog>
  </div>
</template>

<script>
import { eventBus } from '../main'

export default {
  data: () => ({
    showDialog: false,
    item: {
      name: '',
      count: ''
    },
    items: [
      '',
      ''
    ]
  }),
  created () {
    eventBus.$on('showDialog', (showDialog) => {
      this.showDialog = showDialog
    })
  }
}
</script>

<style lang="scss" scoped>
.md-dialog {
    width: 40%;
    overflow-y: auto;
}

.md-dialog-actions {
    padding: 8px 24px;
}

.md-button .md-ripple {
    padding: 0 20px !important;
}
</style>



