<template>
  <div>
    <md-app-content>
        <div class="breadcrumbs">
        <div class="breadcrumbs__item">
          <p class="breadcrumbs__item-large"></p>
          <div>
            <router-link to="/" class="breadcrumbs__link"></router-link> /
            <span v-if="currentDepartment.warehouse"></span>
            <span v-else></span>
            / {{ currentDepartment.name }} /
            <span class="breadcrumbs__item-bold"></span>
          </div>
        </div>
        <md-button class="md-raised button"></md-button>
      </div>
      <md-progress-spinner md-mode="indeterminate" v-if="loading" class="spinner"></md-progress-spinner>
      <div v-else>
        <section class="cards">
          <md-card v-for="worker in workers" :key="worker.id" class="card">
            <md-card-header>
              <md-card-media>
                <img src="../assets/images/person.svg" class="card__photo">
              </md-card-media>
              <md-card-header-text class="card__header">
                <div class="md-body-2">{{ worker.name }} {{ worker.surname }}</div>
                <br/>
                <div class="md-body-1">{{ worker.role }}</div>
                <div class="md-body-1">.: {{ worker.phoneNumber }}</div>
              </md-card-header-text>
            </md-card-header>
            <md-divider></md-divider>
            <md-card-actions>
              <md-button class="card__button"></md-button>
            </md-card-actions>
          </md-card>
        </section>
      </div>
    </md-app-content>
  </div>
</template>

<script>
import departmentsService from '../services/departmentsService'
import store from '../store/store'

export default {
  data: () => ({
    workers: {},
    loading: false
  }),
  created () {
    this.fetchData()
  },
  computed: {
    currentDepartment: () => {
      return store.getters.currentDepartment
    }
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    async fetchData () {
      this.loading = true
      const response = await departmentsService.getDepartmentWorkers(this.$route.params.id)
      this.loading = false
      this.workers = response.data.responseData
    }
  }
}
</script>



